@import "colors";
@import "fonts";

html, body {
  overflow: hidden;
}

body {
  background-color: $darker-green;
  color: $milky-white;
  font-family: "Calamity", Roboto, sans-serif;
  height: 100vh;
  margin: 0;
  overscroll-behavior: none;
}

#root {
  height: 100%;
  overflow: hidden;
}

.main {
  display: flex;
  flex-flow: column nowrap;
  margin: auto;
  height: 100%;
  max-width: 960px;
  overflow: hidden;
  padding: 0 32px;
  .header {
    @include f-main;
    align-items: center;
    display: flex;
    height: 64px;
    line-height: 0;
  }
  .seedinfo {
    @include f-sub;
    color: $pale-green;
    height: 32px;
  }
  .history {
    flex: 1 0 auto;
    max-height: 32vh;
    overflow: hidden;
    .historylist {
      width: calc(100% + 17px);
      overflow-x: hidden;
      overflow-y: scroll;
    }
  }
  .history,
  .shrine.current {
    max-width: 720px;
    margin: 0 auto;
    width: 100%;
  }

  .history .shrine,
  .shrine.current {
    display: grid;
    grid-template-columns: 40px 1fr 32px 128px;
    gap: 0 8px;
    align-items: center;
    width: calc(100% - 16px);
    @media (max-width: 480px) {
      grid-template-columns: 40px 1fr 32px 128px;
    }
  }
}

.worldmap {
  margin: 24px 0 0;
  flex: 1 1 auto;
  min-height: 1px;

  .mapframe {
    background-color: transparent;
    border: none;
    width: 100%;
    height: 100%;
  }
  .mapwrapper {
    background-image: url(../assets/background.png);
    border-radius: 8px;
    height: 100%;
    max-height: 480px;
    overflow: hidden;
    width: 100%;
    &:after {
      content: "Get ready...";
      display: flex;
      flex-flow: row nowrap;
      justify-content: center;
      align-items: center;
      margin: auto;
      position: relative;
      top: 50%;
      transform: 0, -50%;
    }
  }
}

